import React, { useState } from 'react';
import { Popover, PopoverHeader, PopoverBody } from 'reactstrap';
import { copyToClipboard } from "../../../lib/clipboard";

import {
  FacebookShareButton,
  TwitterShareButton,
  WhatsappShareButton,
  TwitterIcon,
  FacebookIcon,
  WhatsappIcon
} from "react-share";

function SharePopover(props) {
  const { popoverOpen, setPopoverOpen, personName } = props;
  const title = `How generous is ${personName}? `;
  const [copied, setCopied] = useState(false);

  return (
    <Popover placement="top" isOpen={popoverOpen} target="shareButton" toggle={() => setPopoverOpen(!popoverOpen)}>
      <PopoverHeader>Share this comparison</PopoverHeader>
      <PopoverBody>

        <FacebookShareButton url={window.location.href} quote={title}>
          <FacebookIcon size={32} round />
        </FacebookShareButton>

        <TwitterShareButton url={window.location.href} title={title}>
          <TwitterIcon size={32} round />
        </TwitterShareButton>

        <WhatsappShareButton url={window.location.href} title={title}>
          <WhatsappIcon size={32} round />
        </WhatsappShareButton>

        <div className={`copyIcon ${copied ? "copied" : ""}`} onClick={() => { 
          copyToClipboard(window.location.href);
          setCopied(true);
          setTimeout(() => {
            setCopied(false);
          }, 250);
        }}>
          <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="copy" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" className="svg-inline--fa fa-copy fa-w-14 fa-9x"><path fill="currentColor" d="M433.941 65.941l-51.882-51.882A48 48 0 0 0 348.118 0H176c-26.51 0-48 21.49-48 48v48H48c-26.51 0-48 21.49-48 48v320c0 26.51 21.49 48 48 48h224c26.51 0 48-21.49 48-48v-48h80c26.51 0 48-21.49 48-48V99.882a48 48 0 0 0-14.059-33.941zM266 464H54a6 6 0 0 1-6-6V150a6 6 0 0 1 6-6h74v224c0 26.51 21.49 48 48 48h96v42a6 6 0 0 1-6 6zm128-96H182a6 6 0 0 1-6-6V54a6 6 0 0 1 6-6h106v88c0 13.255 10.745 24 24 24h88v202a6 6 0 0 1-6 6zm6-256h-64V48h9.632c1.591 0 3.117.632 4.243 1.757l48.368 48.368a6 6 0 0 1 1.757 4.243V112z"></path></svg>
        </div>

      </PopoverBody>
    </Popover>
  );
}

export default SharePopover;
